<template>
	<div>
		<div class="emb-card pa-4 search-box-wrap">
			<div class="d-flex justify-start align-center">
				<div class="dash-title-wrap">
					<h5 class="dash-title mb-0">Our partners</h5>
				</div>
				<div class="search-input d-flex justify-space-between align-center">
					<div class="input-wrap">
					</div>
					<div class="action-btn-wrap">
						<v-btn color="primary"  to="/admin-panel/company-add">
							Add  Company
							<v-icon>add</v-icon>
						</v-btn>
					</div>
				</div>
			</div>
		</div>
		<v-layout row wrap class="pt-12 ma-0">
		</v-layout>
	
		<company-list :data="listData" :gridListView="gridView" :cols="6" :colxl="3" :collg="3" :colmd="4" :colsm="4" :colxs="12">
		</company-list>
	</div>
</template>

<script>
import companyList from "./companyList.vue";
import company from "Api/company";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      typeItems: ["Men", "Women", "Gadgets"],
      recentItems: ["This Week", "This Month", "Past Month"],
      noOfItems: ["10", "20", "30"],
      gridView: true,
      listView: false,
            listData: []
    };
  },
  computed: {
    ...mapGetters(["linksformbackend"])
  },
  components: {
    companyList
  },
  methods: {
    switchToGridView(value) {
      this.gridView = value;
    },
    switchToListView(value) {
      this.gridView = value;
    }
  },
  async created () {
    try {
      const res = await company.getCompany()
      // console.log()
      res.data.data.forEach(el => {
        console.log(el)
        this.listData.push({
          id: el._id,
          image: this.linksformbackend+el.logo,
          name:el.name
        })
      });
    } catch (err) {
      console.log(err.message)
    }
    
  }
};
</script>

